import React from 'react'
import './styles/card.css'

class Card extends React.Component {
  render() {
    return (
      <React.Fragment>
        <div className="containerCard">
          <div className="card__title">
            <p className="card__title-date">2021-01-26</p>
            <p className="card__title-click">click - 03</p>
          </div>
          <div className="card__url">
            <p>Hola.com</p>
            <p className="card__url-origin">https://hola.com</p>
            <p><a className="card__url-modify" href="/">https://cd0.co/zonsygi</a></p>
          </div>
          <div className="card__btn bm-4">
            <button className="btn btn_line">Copiar</button>
            <button className="btn btn_line">Cambiar</button>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export default Card