import React from 'react'
import HeaderTC from '../components/headerTC'
import './styles/termOfUse.css'

class TermOfUse extends React.Component {
  render() {
    return (
      <React.Fragment>
        <HeaderTC title="Términos de Uso" />
        <div className="containerTerm">
          <h5>
              1) SOBRE NOSOTROS
          </h5>
          <p>
              El Proveedor de servicios opera el sitio web y es responsable de la
              adecuada prestación de los servicios electrónicos del sitio web.
              Además del proveedor de servicios, el sitio web presenta a los
              clientes, es decir, terceros independientes del proveedor de
              servicios que pueden utilizar los servicios electrónicos del sitio
              web a través del sitio web.
          </p>
          <p>
              Los datos personales procesados &#8203;&#8203;en el sitio web para implementar
              las disposiciones del mismo son administrados por el proveedor de
              servicios. Los datos personales se procesan para los fines, en la
              medida y en base a los principios y reglas especificados en la
              declaración de privacidad publicada en el sitio web. Entre otras
              cosas, la declaración de privacidad establece cómo el Administrador
              procesa los datos personales en el sitio web, incluido el marco, los
              propósitos y el alcance del procesamiento de datos personales y los
              derechos a los que pertenecen dichos datos personales, así como
              informa sobre qué cookies y En el sitio web se utilizan herramientas
              analíticas. El uso del sitio web es voluntario. La divulgación de
              datos personales por parte del usuario del sitio web también es
              voluntaria.
          </p>
          <br></br>
          <h5>
              2) DEFINICIONES
          </h5>
          <p>
              1. Las definiciones utilizadas a lo largo de este Reglamento tienen
              los siguientes significados:
          </p>
          <p>
              a. BLOG : un servicio electrónico, un blog web disponible en el
              sitio web para sus clientes que contiene artículos que describen
              cómo se pueden utilizar los enlaces abreviados.
          </p>
          <p>
              b. DÍA LABORAL : un día entre el lunes y el viernes, excepto los
              días festivos.
          </p>
          <p>
              c. CUENTA : un servicio electrónico, una colección de recursos en el
              sistema de comunicación de datos del proveedor de servicios donde se
              recopilan los datos proporcionados por el cliente y la información
              sobre los enlaces abreviados, identificados con un nombre individual
              (inicio de sesión) y una contraseña asignada por el proveedor de
              servicios.
          </p>
          <p>
              d. NEWSLETTER - un servicio electrónico que permite a todos los
              clientes recibir automáticamente los sucesivos boletines con
              información sobre novedades.
          </p>
          <p>e. REGLAMENTOS : este Reglamento del sitio web.</p>
          <p>
              f. SITIO WEB o SITIO, Cd0 (cd0.co) : sitio web del proveedor de
              servicios disponible en
              <a href=" https://cd0.co/" target="_blank"> https://cd0.co/</a>.
          </p>
          <p>
              g. ACORTAMIENTO DE VÍNCULOS : un servicio electrónico que permite a
              todos los clientes reducir la longitud del vínculo seleccionado.
          </p>
          <p>
              h. SERVICIO ELECTRÓNICO : un servicio proporcionado electrónicamente
              por el proveedor de servicios al cliente a través del sitio web como
              se establece en este documento.
          </p>
          <p>
              i. CLIENTE : una persona física que tiene plena capacidad jurídica
              y, cuando así lo estipulan las leyes de aplicación general, también
              una persona física con capacidad jurídica limitada que utiliza o va
              a utilizar un Servicio electrónico.
          </p>
          <br></br>
          <h5>3) ACERCA DE Cd0</h5>
          <p>
              1. Cd0.co es un portal que permite a los Clientes acortar los
              enlaces que hacen referencia a otras páginas web, así como agregar
              sus propios dominios y crear enlaces cortos dentro de dichos
              dominios.
          </p>
          <p>
              2. No es posible celebrar un contrato de compra en el sitio web.
          </p>
          <p>
              3. El Proveedor de Servicios es responsable de la prestación de los
              Servicios Electrónicos aquí especificados y está obligado a
              proporcionar los mismos sin defecto alguno.
          </p>
          <br></br>
          <h5>4) TÉRMINOS Y CONDICIONES GENERALES DE USO DEL SITIO WEB</h5>
          <p>
              1. El Cliente está obligado a utilizar el Sitio Web de forma
              legítima y de buena educación, respetando los derechos de
              personalidad, derechos de autor y derechos de propiedad intelectual
              del Proveedor de Servicios y de terceros. El cliente está obligado a
              proporcionar sus datos reales. El Cliente no está autorizado a
              proporcionar ningún contenido ilegítimo.
          </p>
          <p>
              2. Requisitos técnicos para utilizar el sistema de comunicación de
              datos del Proveedor de servicios:
          </p>
          <p>
              (1) computadora, computadora portátil, tableta, teléfono inteligente
              o cualquier otro dispositivo multimedia con acceso a Internet;
          </p>
          <p>
              (2) navegador web: Mozilla Firefox 20.0 o superior (estable) o
              Internet Explorer 9.0 o superior (estable), Google Chrome 25.0. o
              superior (estable);
          </p>
          <p>(3) cookies y Javascript habilitados en el navegador web.</p>
          <br></br>
          <h5>
              5) ACORTAR ENLACES, AÑADIR SUS PROPIOS DOMINIOS Y CREAR ENLACES
              CORTOS DENTRO DE DICHOS DOMINIOS
          </h5>
          <p>
              1. El servicio de acortamiento de enlaces se puede utilizar después
              de introducir un enlace en el campo de edición y hacer clic en el
              campo de acción. El servicio de acortamiento de enlaces se puede
              usar solo una vez y se cancela una vez que se usa.
          </p>
          <p>
              2. El enlace abreviado se registra en la base de datos MySQL del
              proveedor de servicios.
          </p>
          <p>
              3. El servicio de acortamiento de enlaces se proporciona de forma
              gratuita durante un período de tiempo indefinido.
          </p>
          <p>
              4. Además del servicio de acortamiento de enlaces, el cliente
              también puede comprobar qué enlace / dirección URL representa un
              enlace abreviado.
          </p>
          <p>
              5. El Cliente también puede agregar su propio dominio y crear
              enlaces cortos dentro de dicho dominio. Para este propósito, el
              dominio que el Cliente desea agregar debe ser redirigido a la IP del
              Proveedor de Servicios o el CNAME (configuración) del dominio debe
              modificarse incluyendo una referencia al dominio del Proveedor de
              Servicios.
          </p>
          <p>
              6. El Cliente también puede obtener una vista previa de las
              estadísticas de clics en su enlace abreviado.
          </p>
          <br></br>
          <h5>6) DESHABILITAR Y ELIMINAR ENLACES DE LA BASE DE DATOS</h5>
          <p>1. El proveedor de servicios puede desactivar los enlaces que:</p>
          <p>- se hayan incluido en una lista negra;</p>
          <p>
              - se ha informado que es sospechoso (después de que se determina que
              el informe es correcto);
          </p>
          <p>- contiene más de una referencia;</p>
          <p>- se ha acortado más de una vez;</p>
          <p>- se ha informado como potencial malicioso / spam, etc .;</p>
          <p>
              - se refiere a contenidos inaceptables (pornografía, violencia,
              etc.);
          </p>
          <p>- no cumple con la presente.</p>
          <br></br>
          <h5>7) DERECHOS DE AUTOR</h5>
          <p>
              1. Los derechos de autor y los derechos de propiedad intelectual del
              sitio web en su conjunto y de cualquier parte del mismo, incluido
              cualquier contenido, obra de arte, obra, patrón y marca comercial
              que se muestre como parte del mismo, son propiedad del Proveedor de
              servicios u otros terceros elegibles. y están sujetos a derechos de
              autor y otras leyes de aplicación general. La protección otorgada al
              Sitio Web comprende todas las formas de su expresión.
          </p>
          <p>
              2. Las marcas comerciales propiedad del Proveedor de servicios y de
              terceros deben usarse de acuerdo con las leyes aplicables.
          </p>
          <br></br>
        </div>
      </React.Fragment>
    )
  }
}

export default TermOfUse