import React from 'react'
import HeaderTC from '../components/headerTC'
import './styles/termOfUse.css'

class Privacy extends React.Component {
  render() {
    return (
      <React.Fragment>
        <HeaderTC title="Política de privacidad"/>
        <div className="container">
          <div className="containerTerm">
            <h5>
                1) DISPOSICIONES GENERALES
            </h5>
            <p>
                1. Esta Política de Privacidad es solo para información, lo que no
                significa que no surja ninguna obligación para los Usuarios del
                Sitio Web a continuación. Establece, sobre todo, las normas por las
                que el administrador del sitio web procesa los datos personales,
                incluido el marco, los objetivos y el alcance del procesamiento de
                datos personales y los derechos de las personas a las que pertenece
                dicho procesamiento, y proporciona información sobre las cookies. y
                herramientas analíticas utilizadas en el sitio web.
            </p>
            <p>
                2. El uso del sitio web es voluntario. El suministro de datos
                personales por parte del usuario del sitio web también es
                voluntario, salvo dos excepciones:
            </p>
            <p>
                (1) Contratación con el Administrador: no proporcionar los datos
                personales requeridos para la firma y ejecución del Contrato de
                Servicios Electrónicos con el Administrador en los casos y en la
                medida especificados en el Sitio Web y en el Reglamento del Sitio
                Web y esta Política de Privacidad lo hace imposible firmar tal
                contrato. En tal caso, el envío de datos personales es un requisito
                contractual, y si la persona a la que pertenecen dichos datos está
                dispuesta a firmar un contrato con el Administrador, el
                Administrador está obligado a proporcionar todos los detalles
                requeridos. En cada caso, el alcance de los datos necesarios para la
                firma de un contrato se especifica de antemano en el Sitio Web y en
                el Reglamento del Sitio Web;
            </p>
            <p>
                (2) Obligaciones legales del administrador: el envío de datos
                personales es un requisito legal según las regulaciones generalmente
                aplicables, por lo que el administrador está obligado a procesar los
                datos personales (por ejemplo, como parte de sus obligaciones
                fiscales o contables) y la falta de envío de los mismos hace que sea
                imposible para ellos cumplir con dichas obligaciones.
            </p>
            <p>
                3. El Administrador tiene sumo cuidado para proteger los intereses
                de las personas cuyos datos personales procesa el Administrador, en
                particular el Administrador es responsable de asegurarse de que los
                datos recopilados por él:
            </p>
            <p>(1) se procesen de acuerdo con las leyes aplicables;</p>
            <p>
                (2) se recopila para fines legítimos específicos y no se procesa
                para ningún otro propósito;
            </p>
            <p>
                (3) es sustancialmente correcto y adecuado para los fines para los
                que se procesan dichos datos;
            </p>
            <p>
                (4) se almacena en un formato que permite identificar a las personas
                a las que pertenece por un período no superior al que puede ser
                necesario para lograr el propósito de su procesamiento; y
            </p>
            <p>
                (5) se procesa, utilizando los medios técnicos u organizativos
                adecuados, para garantizar adecuadamente la seguridad de los datos
                personales, incluida la protección de dichos datos contra cualquier
                procesamiento no permitido o ilegítimo o pérdida, destrucción o daño
                inadvertidos.
            </p>
            <p>
                4. Considerando la naturaleza, alcance, contexto y propósitos del
                procesamiento de datos y el riesgo, por probable e inminente que
                sea, de violar cualquier derecho o libertad de las personas físicas,
                el Administrador implementa las medidas técnicas y organizativas
                apropiadas para que el procesamiento de datos cumpla con este
                reglamento. y poder probarlo. Si es necesario, estas medidas se
                revisan y actualizan. El Administrador toma medidas técnicas para
                evitar que personas no autorizadas intercepten o alteren cualquier
                dato personal que se transmita electrónicamente.
            </p>
            <p>
                5. Todas las palabras, expresiones y acrónimos utilizados en esta
                Política de privacidad y que comiencen con una letra mayúscula (por
                ejemplo, Usuario, Tienda en línea, Servicio electrónico) deben
                considerarse como se definen en las Regulaciones del sitio web
                disponibles en el Sitio web.
            </p>
            <br></br>
            <h5>2) MARCO DE PROCESAMIENTO DE DATOS</h5>
            <p>
                1. El Administrador tiene derecho a procesar datos personales si y
                en la medida en que se cumpla al menos una de las siguientes
                condiciones:
            </p>
            <p>
                (1) la persona a la que pertenecen dichos datos ha dado su
                consentimiento para que sus datos personales sean procesados &#8203;&#8203;para
                uno o más propósitos;
            </p>
            <p>
                (2) el procesamiento de datos es necesario para ejecutar el contrato
                al que pertenecen dichos datos o para tomar cualquier medida a
                petición de la persona a la que pertenecen dichos datos antes de
                firmar el contrato;
            </p>
            <p>
                (3) el procesamiento de datos es necesario para cumplir con la
                obligación legal impuesta al Administrador; o
            </p>
            <p>
                (4) el procesamiento de datos es necesario para los fines
                determinados por los intereses legítimos perseguidos por el
                Administrador o cualquier tercero, excepto cuando dichos intereses
                se vean eclipsados &#8203;&#8203;por los intereses o los derechos y libertades
                fundamentales de la persona a la que pertenecen dichos datos
                elegibles para la protección, especialmente si tal persona es un
                niño.
            </p>
            <p>
                2. El tratamiento de los datos personales por parte del
                Administrador está, en cada caso, sujeto a la existencia de al menos
                uno de los requisitos previos indicados en 2.1 del presente. Un
                marco específico para el procesamiento de los datos personales de
                los usuarios del sitio web por parte del administrador se establece
                en la siguiente cláusula del presente, es decir, con respecto a un
                propósito específico de procesamiento de datos por parte del
                administrador.
            </p>
            <br></br>
            <h5>3) DESTINATARIOS DE LOS DATOS EN EL SITIO WEB</h5>
            <p>
                1. Para garantizar el correcto funcionamiento del sitio web,
                incluida la ejecución de sus contratos, el administrador debe
                utilizar servicios externos (como software de terceros). El
                Administrador utiliza solo los servicios proporcionados por dichos
                procesadores de datos que pueden garantizar adecuadamente que se
                implementan las medidas técnicas y organizativas adecuadas para
                garantizar el cumplimiento del procesamiento de datos con los
                requisitos del Reglamento GDPR y proteger los derechos de las
                personas a las que pertenecen los datos.
            </p>
            <p>
                2. El Administrador no divulga datos en cada caso y a todos los
                destinatarios o categorías de destinatarios especificados en la
                Política de Privacidad; el Administrador divulga datos solo si es
                necesario para perseguir un propósito específico de procesamiento de
                datos y solo en la medida en que sea necesario para lograr dicho
                propósito.
            </p>
            <p>
                3. Los datos personales de los usuarios del sitio web se pueden
                divulgar a los siguientes destinatarios o categorías de
                destinatarios:
            </p>
            <p>
                a. Proveedores de servicios que proporcionan al Administrador las
                soluciones técnicas, de TI y organizativas que necesita el
                Administrador para llevar a cabo su actividad comercial, incluido el
                Sitio web y, en consecuencia, los Servicios electrónicos (en
                particular, los proveedores del software utilizado para ejecutar el
                Sitio web, los proveedores de correo electrónico ). El Administrador
                divulga los datos personales del Usuario a un proveedor seleccionado
                sólo si y en la medida en que sea necesario para lograr un propósito
                específico del procesamiento de datos a continuación.
            </p>
            <br></br>
            <h5>4) PERFIL DE DATOS EN EL SITIO WEB</h5>
            <p>
                1. En su sitio web, el administrador puede utilizar la elaboración
                de perfiles de datos con fines de marketing directo, pero las
                decisiones del administrador basadas en el mismo no se aplican a la
                posibilidad de suscribirse a servicios electrónicos en el sitio web.
            </p>
            <p>
                2. La elaboración de perfiles de datos en el sitio web implica el
                análisis automático o la previsión del comportamiento de una persona
                en el sitio web, por ejemplo, cuando dicha persona visita una página
                específica del sitio web o al analizar el historial de las acciones
                realizadas por dicha persona en el sitio web hasta el momento. .
                Dicho perfil de datos es posible si el Administrador está en
                posesión de los datos personales de una persona que se pueden
                utilizar, por ejemplo, para enviarle un boletín informativo sobre
                nuevos productos.
            </p>
            <p>
                3. La persona a la que pertenecen los datos tiene derecho a oponerse
                a las decisiones basadas en el procesamiento automatizado de datos,
                incluida la elaboración de perfiles de datos, y conllevan efectos
                legales para dicha persona o, de manera similar, tienen un impacto
                sustancial en las mismas.
            </p>
            <br></br>
            <h5>5) DERECHOS DE LA PERSONA A LA QUE SE REFIEREN LOS DATOS</h5>
            <p>
                1. Derecho a acceder, rectificar, limitar, eliminar o mover : la
                persona a la que pertenecen los datos tiene derecho a solicitar al
                Administrador que le proporcione acceso a sus datos personales, que
                rectifique o elimine dichos datos ( “derecho al olvido”), a limitar
                u oponerse al tratamiento de los mismos, así como a la transferencia
                de sus datos.
            </p>
            <p>
                2. Derecho a oponerse al marketing directo : si los datos personales
                se procesan con fines de marketing directo, la persona a la que
                pertenecen los datos tiene derecho a presentar en cualquier momento
                una objeción contra el procesamiento de sus datos personales con el
                fin de dicho marketing. incluida la elaboración de perfiles de
                datos, en la medida en que el procesamiento de datos esté asociado
                con dicho marketing directo.
            </p>
            <p>
                6. En lo que respecta al ejercicio de los derechos descritos en esta
                cláusula de la Política de privacidad, el Administrador puede ser
                contactado por escrito o el formulario de contacto disponible en el
                Sitio web.
            </p>
            <br></br>
            <h5>6) COOKIES EN EL SITIO WEB, DATOS DE RENDIMIENTO Y ANÁLISIS</h5>
            <p>
                1. Los archivos de cookies (cookies) son informaciones breves en
                forma de archivos de texto breves enviados por el servidor y
                guardados en el lado del visitante del sitio web (por ejemplo, en el
                disco duro de la computadora o computadora portátil o en la tarjeta
                de memoria del teléfono inteligente, según el dispositivo del
                visitante del sitio web usos). Los detalles de las cookies y el
                historial de cómo se crearon se pueden encontrar, por ejemplo, aquí:
                <a href="https://en.wikipedia.org/wiki/HTTP_cookie" target="_blank">https://en.wikipedia.org/wiki/HTTP_cookie</a>.
            </p>
            <p>
                2. El Administrador puede procesar los datos contenidos en los
                archivos de cookies cuando los usuarios visitan el Sitio Web para
                los siguientes propósitos:
            </p>
            <p>
                a. Para identificar a los usuarios que han iniciado sesión en el
                sitio web y mostrar que han iniciado sesión;
            </p>
            <p>
                b. Para personalizar el contenido del sitio web para reflejar las
                preferencias personales del usuario (por ejemplo, un idioma
                preferido) y optimizar el uso de las páginas del sitio web;
            </p>
            <p>
                c. Mantener estadísticas anónimas de cómo se utiliza el sitio web;
            </p>
            <p>
                d. Utilizar el remarketing, es decir, revisar las características de
                comportamiento de los usuarios del sitio web analizando de forma
                anónima lo que hacen (no. Visitas recurrentes a páginas específicas,
                palabras clave, etc.) para crear sus perfiles y proporcionarles
                anuncios personalizados incluso si visitan otros sitios web.
                cubierto por la red de visualización de Google Inc.
            </p>
            <p>
                3. Como regla general, la mayoría de los navegadores web disponibles
                comercialmente aceptan guardar cookies de forma predeterminada.
                Todos pueden decidir cómo utilizarán los archivos de cookies
                mediante la configuración adecuada en su navegador. Esto significa
                que es posible, por ejemplo, limitar parcialmente (por ejemplo, a
                ciertos momentos) o deshabilitar completamente el almacenamiento de
                cookies; en el último caso, esto podría afectar algunas de las
                funcionalidades del Sitio Web.
            </p>
            <p>
                4. La configuración de cookies del navegador web es crucial para
                determinar si se ha otorgado o no consentimiento para que nuestro
                sitio web utiliza archivos de cookies; según las regulaciones, dicho
                consentimiento se puede otorgar mediante la configuración
                correspondiente en el navegador web. Si se rechaza dicho
                consentimiento, la configuración de cookies del navegador web debe
                cambiarse en consecuencia.
            </p>
            <p>
                5. Los detalles de cómo se puede cambiar la configuración de las
                cookies y cómo se pueden eliminar las cookies de los navegadores web
                más populares están disponibles en la sección de Ayuda del navegador
                web correspondiente y en las siguientes páginas (lo único que debe
                hacer es hacer clic el enlace correspondiente a continuación):
            </p>
            <p>
                a.<a href="https://support.google.com/chrome/answer/95647?hl=en" target="_blank">para Chrome</a>
            </p>
            <p>
                b.<a href="https://support.mozilla.org/en-US/kb/enable-and-disable-cookies-website-preferences" target="_blank">para Firefox</a>
            </p>
            <p>
                c.<a href="http://windows.microsoft.com/en-en/windows7/block-enable-or-allow-cookies" target="_blank">para Internet Explorer</a>
            </p>
            <p>
                d.<a href="https://help.opera.com/en/latest/" target="_blank">para Opera</a>
            </p>
            <p>
                e.<a href="http://support.apple.com/kb/PH5042" target="_blank">para Safari</a>
            </p>
            <p>
                f.<a href="http://windows.microsoft.com/pl-pl/windows-10/edge-privacy-faq" target="_blank">para Microsoft Edge</a>
            </p>
            <p>
                6. En el sitio web, el administrador puede utilizar Google Analytics
                o Universal Analytics proporcionado por Google Inc. (1600
                Amphitheatre Parkway, Mountain View, CA 94043, EE. UU.). Estos
                servicios ayudan al administrador a analizar el tráfico en el sitio
                web. Los datos así recopilados se procesan como parte de dichos
                servicios de forma anónima (son los denominados datos operativos,
                que no son suficientes para identificar a una persona) para generar
                estadísticas que ayuden a administrar el Sitio Web. Dichos datos son
                colectivos y anónimos, es decir, están libres de características
                (como datos personales) que puedan utilizarse para identificar a los
                visitantes del sitio web. Al utilizar los servicios descritos
                anteriormente para su sitio web, el administrador recopila datos
                tales como las fuentes y los medios de los que los visitantes del
                sitio web han sido atraídos, así como cómo se comportan dichos
                visitantes en el sitio web.
            </p>
            <p>
                7. Es posible desactivar fácilmente la divulgación de información
                sobre lo que hace en el sitio web a Google Analytics; para este
                propósito, debe instalar un complemento de navegador disponible de
                Google Inc. en:
                <a href="https://tools.google.com/dlpage/gaoptout?hl=en" target="_blank">https://tools.google.com/dlpage/gaoptout?hl=en</a>.
            </p>
            <p>
                8. En su sitio web, el Administrador puede utilizar los servicios
                Pixel proporcionados por Facebook Ireland Limited (4 Grand Canal
                Square, Grand Canal Harbour, Dublín 2, Irlanda) y Twitter Inc, con
                domicilio social en EE. UU. (San Francisco, California) ,
                respectivamente. Los servicios ayudan al Administrador a medir la
                eficacia de los anuncios y a saber qué hacen los visitantes en el
                sitio web, así como a mostrar anuncios personalizados. Los detalles
                de cómo funciona Facebook Pixel se pueden encontrar en:
                <a href="https://www.facebook.com/business/help/742478679120153?helpref=page_content" target="_blank">https://www.facebook.com/business/help/742478679120153?helpref=page_content</a>, y cómo funciona Twitter Pixel aquí:
                <a href="https://twitter.com/piksel" target="_blank">https://twitter.com/piksel</a>.
            </p>
            <p>
                9. El funcionamiento de los servicios de píxeles de Facebook y
                Twitter se puede ajustar mediante la configuración de anuncios
                relevantes en sus perfiles de Facebook.com y Twitter.com.
            </p>
            <br></br>
            <h5>7) DISPOSICIONES FINALES</h5>
            <p>
                1. El sitio web puede contener referencias a otros sitios web.
                Siempre que visite otros sitios web, el Administrador recomienda
                familiarizarse con las políticas de privacidad relevantes allí. Esta
                Política de privacidad se aplica solo al sitio web del
                administrador.
            </p>
            <br></br>
            <p>¡Gracias por leer atentamente!</p>
            <p>Si tiene alguna pregunta, no dude en contactarnos.</p>
            <p>Esperamos poder cooperar con usted</p>
            <br></br>
            <p>Equipo Cd0</p>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export default Privacy