import React from 'react'
import './styles/footer.css'
import logojc from '../images/JC_negro.png'

class Footer extends React.Component {
  render() {
    return (
      <React.Fragment>
        <div className="containerFooter">
          <div className="footer">
            <p>V 1.0.1</p>
            <p>By <a href="https://jose.caicedo.dev/">
              <img src={logojc} alt="logoJC"></img> 
            </a></p>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export default Footer