import React from 'react'
import './styles/cuturl.css'

class CutUrl extends React.Component {
  render() {
    return (
      <React.Fragment>
        <div className="inputs">
          <input
          className="inputs__Form"
          type="text" 
          placeholder="Pega la url larga y acortarla"></input>
          <button className="btn btn-primary">acortar</button>
        </div>
        <p className="inputs__Description">Al usar nuestro servicio, acepta los <a href="http://">Términos de uso</a> y la <a href="http://">Política de privacidad</a>.</p>
      </React.Fragment>
    )
  }
}

export default CutUrl